import React, { useEffect, useState } from 'react'
import { Popover } from 'react-tiny-popover'
import { HexColorPicker } from "react-colorful";
import moment from 'moment';
import Cookies from 'js-cookie'
import dayjs from 'dayjs';
// import { glob, globSync, globStream, globStreamSync, Glob } from 'glob'



const Home = () => {

    const [isPopoverOpen , setIsPopoverOpen] = useState(false)
    const [color, setColor] = useState("#aabbcc");

    useEffect(()=>{
       console.log( Cookies.get("name"),"55");

  
    },[])

  return (
    <div>
      <Popover isOpen={isPopoverOpen}
        positions={[ 'right']}
        padding={10} 
        containerStyle={{background : color }}
        content={<div>Hi! I'm popover content.</div>}
    >
        <button onClick={() => setIsPopoverOpen(!isPopoverOpen)}>
            Click me! to open popup
        </button>
    </Popover>


    <hr/>
    <HexColorPicker color={color} onChange={setColor} />


    <hr/>

    <div>
      current moment Date : {moment(Date.now()).format('MMMM Do YYYY, h:mm:ss a')}
    </div>
    <div>
      current Dayjs Date :  {dayjs(Date.now()).format('{YYYY} MM-DDTHH:mm:ss SSS [Z] A')}
    </div>
    </div>
  )
}

export default Home
